import React, { useState, useEffect } from "react";
import { Button } from "react-bootstrap";
import { ReactComponent as CartVacio } from "../../assets/svg/cart-empty.svg";
import { ReactComponent as CartLleno } from "../../assets/svg/cart-full.svg";
import { ReactComponent as Close } from "../../assets/svg/close.svg";
import { ReactComponent as Vaciar } from "../../assets/svg/garbage.svg";
import {
  removeArrayDuplicates,
  countDuplicatesItemArray,
  removeItemArray,
} from "../../utils/arrayFunc";

import { STORAGE_PRODUCTS, BASE_PATH } from "../../utils/constants";

import "./Cart.scss";

export default function Cart(props) {
  const { productsCard, getProductCard, products } = props;

  const [cartOpen, setCartOpen] = useState(false);
  const widthCartContent = cartOpen ? 400 : 0;
  const [cardTotalPrice, setcardTotalPrice] = useState(0);

  useEffect(() => {
    const productData = [];
    let totalPrice = 0;
    const allProductsId = removeArrayDuplicates(productsCard);

    allProductsId.forEach((productId) => {
      const quantity = countDuplicatesItemArray(productId, productsCard);
      const productValue = {
        id: productId,
        quantity: quantity,
      };
      productData.push(productValue);
    });
    if (!products.loading && products.result) {
      products.result.forEach((product) => {
        productData.forEach((item) => {
          if (product.id == item.id) {
            totalPrice = totalPrice + product.price * item.quantity;
          }
        });
      });
    }
    setcardTotalPrice(totalPrice);
  }, [productsCard, products]);

  const [sinlgeProductCart, setSingleProductCart] = useState([]);
  useEffect(() => {
    const allProductsId = removeArrayDuplicates(productsCard);
    setSingleProductCart(allProductsId);
  }, [productsCard]);

  const openCart = () => {
    setCartOpen(true);
    document.body.style.overflow = "hidden";
  };

  const closeCart = () => {
    setCartOpen(false);
    document.body.style.overflow = "scroll";
  };

  const emptyCart = () => {
    localStorage.removeItem(STORAGE_PRODUCTS);
    getProductCard();
  };

  const increaseQuantity = (id) => {
    const arraytItemsCart = productsCard;
    arraytItemsCart.push(id);
    localStorage.setItem(STORAGE_PRODUCTS, arraytItemsCart);
    getProductCard();
  };

  const decreaseQuantity = (id) => {
    const arraytItemsCart = productsCard;
    const result = removeItemArray(arraytItemsCart, id.toString());
    localStorage.setItem(STORAGE_PRODUCTS, result);
    getProductCard();
  };

  return (
    <>
      <Button variant="link" className="cart">
        {productsCard.length > 0 ? (
          <CartLleno onClick={openCart} />
        ) : (
          <CartVacio onClick={openCart} />
        )}
      </Button>
      <div className="cart-content" style={{ width: widthCartContent }}>
        <CardContentHeader closeCart={closeCart} emptyCart={emptyCart} />
        <div className="cart-content__products">
          {sinlgeProductCart.map((idProductsCart, index) => (
            <CardContentProducts
              key={index}
              products={products}
              idsProductsCart={productsCard}
              idProductCart={idProductsCart}
              increaseQuantity={increaseQuantity}
              decreaseQuantity={decreaseQuantity}
            />
          ))}
        </div>
        <CardContentFooter cardTotalPrice={cardTotalPrice} />
      </div>
    </>
  );
}

function CardContentHeader(props) {
  const { closeCart, emptyCart } = props;
  return (
    <div className="cart-content__header">
      <div>
        <Close onClick={closeCart} />
        <h2>Carrito</h2>
      </div>

      <Button variant="link">
        vaciar
        <Vaciar onClick={emptyCart} />
      </Button>
    </div>
  );
}

function CardContentProducts(props) {
  const {
    products: { loading, result },
    idsProductsCart,
    idProductCart,
    increaseQuantity,
    decreaseQuantity,
  } = props;

  if (!loading && result) {
    return result.map((product, index) => {
      if (idProductCart == product.id) {
        const quantity = countDuplicatesItemArray(product.id, idsProductsCart);
        return (
          <RenderProduct
            key={index}
            product={product}
            quantity={quantity}
            increaseQuantity={increaseQuantity}
            decreaseQuantity={decreaseQuantity}
          />
        );
      }
    });
  }
  return null;
}

function RenderProduct(props) {
  const { product, quantity, increaseQuantity, decreaseQuantity } = props;

  return (
    <div className="cart-content__product">
      <img src={`${BASE_PATH}/${product.image}`} alt={product.name} />
      <div className="cart-content__product-info">
        <div>
          <h3>{product.name.substr(0, 25)}</h3>
          <p>{product.price.toFixed(2)} $/ unidad</p>
        </div>
        <div>
          <p>En carrito : {quantity} unidades</p>
          <div>
            <button onClick={() => increaseQuantity(product.id)}>+</button>
            <button onClick={() => decreaseQuantity(product.id)}>-</button>
          </div>
        </div>
      </div>
    </div>
  );
}

function CardContentFooter(props) {
  const { cardTotalPrice } = props;

  return (
    <div className="cart-content__footer">
      <div>
        <p>Total Aproximado: </p>
        <p>{cardTotalPrice.toFixed(2)} $</p>
      </div>
      <button>Tramitar pedidos</button>
    </div>
  );
}
