import React, { useState, useEffect } from "react";
import Header from "./Components/Header";
import Products from "./Components/Products";
import useFetch from "./hooks/useFetch";
import { urlApiProducts, STORAGE_PRODUCTS } from "./utils/constants";
import { ToastContainer, toast } from "react-toastify";

function App() {
  const products = useFetch(urlApiProducts, null);
  const [productsCard, setProductCard] = useState([]);

  useEffect(() => {
    getProductCard();
  }, []);

  const getProductCard = () => {
    const idsProducts = localStorage.getItem(STORAGE_PRODUCTS);
    if (idsProducts) {
      const idsProductsSplit = idsProducts.split(",");
      setProductCard(idsProductsSplit);
    } else {
      setProductCard([]);
    }
  };

  const addProductCard = (id, name) => {
    const idsProducts = productsCard;
    idsProducts.push(id);
    setProductCard(idsProducts);
    localStorage.setItem(STORAGE_PRODUCTS, productsCard);
    getProductCard();
    toast.success("Añadido Correctamente al Carrito!!");
  };

  return (
    <div>
      <Header
        productsCard={productsCard}
        getProductCard={getProductCard}
        products={products}
      />
      <Products products={products} addProductCard={addProductCard} />
      <ToastContainer
        position="bottom-left"
        autoClose={2500}
        hideProgressBar
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </div>
  );
}

export default App;
